import React, { Component } from 'react'; 
import "./../bootstrap/bootstrapC.css";
import { connect } from 'react-redux'


class Seller extends Component {
    state={
        id:'',
        name:'',
        phone:'',
        about:'',
        img:''
        
    }
    
    componentWillMount(){     

var option = {
    method: 'POST',
    body: JSON.stringify(this.props.match.params),
    headers: {
        'Content-Type': 'application/json'
    }
}

fetch('http://localhost:8000/sellerprofile', option)
    .then(res => res.json())
    .then(data => {
        console.log(data)
        this.setState({id:data._id})
        this.setState({name:data.name})
        this.setState({phone:data.phone})
        this.setState({about:data.about})
        this.setState({img:data.url1})
    })
    .catch(err => console.log(err))
}


    
    render(){      

        return(
            <div className="app">
                <div className="container">
                
                    <div className="detail-container text-center">
                        <div className="profile-pic-container">
                            <img height="150" width="150" src={this.state.img} alt="profile-pic" />
                        </div>

                       <div style={{textAlign:'center'}}>
                            <span className="detail-text-group">
                                    <label htmlFor="exampleInputEmail1">Seller Id</label>
                                    <br/>
                                    <input type="text"  value={this.state.id} disabled name="detail-name" className="detail-input"  />                               
                            </span>
                            <span className="detail-text-group">
                                    <label htmlFor="exampleInputEmail1">Seller Name</label>
                                    <br/>
                                    <input type="text" value={this.state.name} disabled name="detail-name" className="detail-input"  />                               
                            </span>
                            <span className="detail-text-group">
                                    <label htmlFor="exampleInputEmail1">About Seller</label>
                                    <br/>
                                    <input type="text" value={this.state.about} disabled name="detail-info" className="detail-input"  />                               
                            </span>
                            <span className="detail-text-group">
                                    <label htmlFor="exampleInputEmail1">Seller Mobile Number</label>
                                    <br/>
                                    <input type="text" value={this.state.phone} disabled name="detail-mobile" className="detail-input"/>
                            </span>
                       </div>
                        <div className="container">
                        <hr/>
                        </div>

                    </div>

                   

                </div>
            </div>
        );
    }
}
const mapStateToProps = (store) => {
    return {
        ad: store.adsReducer
    }
}
export default connect(mapStateToProps)(Seller);

