import React, { Component } from 'react';
import "./../bootstrap/bootstrapC.css"
import { Link } from 'react-router-dom'
import AliceCarousel from 'react-alice-carousel';
import "react-alice-carousel/lib/alice-carousel.css";

class Content extends Component {
    constructor(props){
        super(props);
        this.state={
            ads:""
        }
    }
    componentDidMount(){

        var option = {
            method: 'POST',
            body: '',
            headers: {
                'Content-Type': 'application/json'
            }
        }
        fetch('http://localhost:8000/allads', option)
        .then(res => res.json())
        .then(data=> {this.setState({ads:data})
        })
        .catch(err => console.log(err))

            let rt = JSON.parse(localStorage.getItem('sa'))
            
        if(rt !== null){
            if(rt.length !== 0){
                document.getElementById('ad').classList.add("display")
                document.getElementById('as').classList.add("hidd")
            }
            if(rt.length === 0 ){
                document.getElementById('ad').classList.add("display")
                document.getElementById('as').classList.add("hidd")
            }

        }

    }    
           allAds=()=>{
               window.location.reload();
                localStorage.removeItem('sa')
            }

    render() {

        let d;
        let e= "Sorry No ads Found matching your criteria" ;
        let searchedAds = JSON.parse(localStorage.getItem('sa'));
        let im = {
            margin: "20px 0",
        }
        if (this.state.ads.length !== 0) {
            for (let i = 0; i <= this.state.ads.length; i++) {
                let adId;
                d = this.state.ads.map((item ,index) =>
                    <div key={index} className="col-md-3" >
                        <div className="card" style={im} >                        
                            <img className="card-img-top" height="150" width="400" src={this.state.ads[index].url1} alt="Card image cap"/>
                            <div className="card-body">
                                <h5 className="card-title text-left">{item.adTitle}</h5><h5 className="card-title text-left">{this.state.ads[index].price} Rs</h5>
                                <p className="text-left">Ad Id : {adId = this.state.ads[index]._id}</p>
                                <p className="card-text text-left">{this.state.ads[index].description}</p>
                                <Link to={"/ad/"+adId} className="btn login-btn float-right" style={{marginTop:"0px"}}>Open Ad</Link>
                            </div>
                        </div>
                    </div>
                )
            }

        } else{
            d = 'Sorry no ads have been posted'
        }
        // Searched Ads

        if (searchedAds !== null) {

            if(searchedAds.length !==0) {
            for (let i = 0; i <=searchedAds.length; i++) {
                e = searchedAds.map((item ,index) =>
                <div key={index} className="col-md-3" >
                {console.log(searchedAds[0]._id)}
                        <div className="card" style={im} >                        
                            <img className="card-img-top" height="150" width="400" src={item.url1} alt="Card image cap"/>
                            <div className="card-body">
                                <h5 className="card-title text-left">{item.adTitle}</h5><h5 className="card-title text-left">{item.price}</h5>
                                <p className="text-left">Ad Id : {item._id}</p>
                                <p className="card-text text-left">{item.description}</p>
                                <Link to={"/ad/"+item._id} className="btn login-btn float-right" style={{marginTop:"0px"}}>Open Ad</Link>
                            </div>
                        </div>
                    </div>
                )
            }
        }
        } else{
                e="asdasd"
        }
        if(searchedAds === null){
            
            e = 'You have not searched for an ad'

        }
        const handleOnDragStart = e => e.preventDefault()
        
        return (

            <div className="App" style={{ textAlign: "center" }}>
                <div className="container" style={{marginBottom:"50px"}}>
                    <AliceCarousel  showSlideInfo={true} buttonsDisabled={true} duration={400} autoPlay={true} autoPlayInterval={5000} mouseDragEnabled >
                        <img  src={require('./../images/ca4.jpg')} height='500' width='1200' onDragStart={handleOnDragStart} className="yours-custom-class" />
                        <img  src={require('./../images/ca3.jpg')} height='500' width='1200' onDragStart={handleOnDragStart} className="yours-custom-class" />
                        <img  src={require('./../images/ca2.jpg')} height='500' width='1200' onDragStart={handleOnDragStart} className="yours-custom-class" />
                        <img  src={require('./../images/ca1.jpg')} height='500' width='1200' onDragStart={handleOnDragStart} className="yours-custom-class" />
                    </AliceCarousel>
                </div>
               <div id="as">
                    
                    <div className="display-4">All Ads</div>
                    <div className="row">
                        {d}
                    </div>
                    <div className="container">
                        <hr/>
                    </div>
               </div>
               
                <div  id="ad" className="hidd">
                    <div className="display-4">Searched Ads</div>
                        <div className="row mt-5 ml-5">
                        {e}
                        </div>

                    <button className="btn login-btn" onClick={this.allAds}>All Ads</button>
                </div>
            </div>

        );

    }
}
export default Content
